import { Extension } from '@tiptap/core';
const TuiFileLink = Extension.create({
  name: `fileLink`,
  addCommands() {
    return {
      setFileLink: fileLink => ({
        state,
        chain
      }) => {
        const {
          selection
        } = state;
        const selectedSize = Math.abs(selection.to - selection.from);
        const whitespace = `<span style="font-size: 15px"> </span>`;
        return (selectedSize > 0 ? chain().toggleMark(`link`, {
          href: fileLink.link
        }, {
          extendEmptyMarkRange: true
        }).setTextSelection(selection.to).insertContent(whitespace) : chain().insertContent(`<a href="${fileLink.link}">${fileLink.name}</a>${whitespace}`)).setTextSelection(selection.to).run();
      }
    };
  }
});

